
import { useState, useEffect } from 'react';
import axios from 'axios';

import SettingProductCreateOption from './SettingProductCreateOption';

const SettingProductAdd = () => {

    //    (, ,  )

    //   
    const [option, setOption] = useState<any>([]);
    const [productCode, setProductCode] = useState("");
    const [productName, setProductName] = useState("");
    const [productPrice, setProductPrice] = useState("");
    const [productMajorCategory, setProductMajorCategory] = useState("");
    const [productMenuCategory, setProductMenuCategory] = useState("");
    const [productImage, setProductImage] = useState<File | null>(null);
    const [menuCategoryList, setMenuCategoryList] = useState<any>([]);
    const [majorCategoryList, setMajorCategoryList] = useState<any>([]);
    useEffect(() => {
        console.log("===============================");
        console.log("optionState");
        console.log(option);
        console.log("===============================");
    }, [option]);
    async function getMajorCategory() {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getMajorCategory`);

            if (response.status === 200) {
                // console.log(response.data);
                setMajorCategoryList(response.data);
            }
        } catch (error) {
            console.error('Error:', error);
        }
    }

    async function getMenuCategoryBymajorCategory(majorCategoryName: string) {
        if (productMajorCategory === majorCategoryName) {
            //      (   )
            setProductMajorCategory("");
            setProductMenuCategory("");
            setMenuCategoryList([]); //    
        } else {
            try {
                setProductMenuCategory("");
                const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getMenuCategoryByMajorCategory?majorCategoryName=${majorCategoryName}`);
                if (response.status === 200) {
                    // subCategoryList    
                    // console.log(response.data);
                    setProductMajorCategory(majorCategoryName);
                    setMenuCategoryList(response.data);
                }
            } catch (error) {
                console.error('Error:', error);
            }
        }
    }

    useEffect(() => {
        console.log("option");
        console.log(option);
    }, [option]);
    //    
    async function sendProductData() {
        let isDefaultMissing = false; //     

        //    
        for (let optionName in option) {
            const category = option[optionName];
            if (!category || category.length === 0) {
                console.log(`${optionName}   .`);
                continue; //       
            }
        
            //     
            for (let i = 0; i < category.length; i++) {
                const optionItems = category[i].options;
                if (!optionItems || optionItems.length === 0) {
                    console.log(`${optionName}  ${category[i].option_name}     .`);
                    continue; //      
                }
        
                //       
                const defaultChecked = optionItems.some((item:any) => item.option_item_isDefault);
                if (!defaultChecked) {
                    alert(`${category[i].option_name}    .`);
                    isDefaultMissing = true;
                    break;
                }
            }
        
            if (isDefaultMissing) {
                break; //      
            }
        }


        console.log(productImage);
        console.log(productName);
        console.log(productPrice);
        console.log(productMenuCategory);
        // console.log(options);
        const formData = new FormData();
        formData.append('menuCode', productCode);
        formData.append('menuName', productName);
        formData.append('menuCategory', productMenuCategory);
        formData.append('price', productPrice);
        formData.append('option', JSON.stringify(option));

        if (productImage) {
            formData.append('image', productImage);
        }

        try {
            // const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/addMenu`, formData, {
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/createMenu`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
            });
            if (response.status === 200) {
                alert(response.data);
                // alert(response.data.message);
                console.log(response.data);
                // window.location.reload();
            }
        } catch (error: any) {
            console.error('Error:', error);
            if (error.response && error.response.status === 409) {
                alert(" ..");
            } else {
                console.log(error);
            }
        }
    }

    return (
        <section className='SettingProductCreate'>
            <h1> </h1>
            {/*     */}
            <section className='w-full'>
                {/*   */}
                <article className='productSettingLine'>
                    <div className='productSettingName'></div>
                    <div className='productSettingValue'>
                        <input
                            type="text"
                            placeholder=''
                            value={productCode}
                            onChange={(e) => setProductCode(e.target.value)} />
                    </div>
                </article>
                <article className='productSettingLine'>
                    <div className='productSettingName'></div>
                    <div className='productSettingValue'>
                        <input
                            type="text"
                            placeholder=''
                            value={productName}
                            onChange={(e) => setProductName(e.target.value)} />
                    </div>
                </article>
                {/*   */}
                <article className='productSettingLine'>
                    <div className='productSettingName'></div>
                    <div className='productSettingValue'>
                        <input
                            className='border border-black'
                            type="number"
                            placeholder=''
                            value={productPrice}
                            onChange={(e) => setProductPrice(e.target.value)} />
                    </div>
                </article>
                {/*   */}
                <article className='productSettingLine'>
                    <div className='productSettingName'></div>
                    <div className='productSettingValue'>
                        {majorCategoryList && majorCategoryList.length > 0 ? (
                            <div className='majorCategoryOutput' onClick={() => {
                                setMajorCategoryList([]);
                            }}>
                                <button> </button>
                                {/* <div></div> */}
                            </div>
                        ) : (
                            <div className='bg-gray-200 flex justify-between'>
                                <button onClick={getMajorCategory}>  </button>
                                {/* <div></div> */}
                            </div>
                        )}
                        {majorCategoryList.map((item: any, index: any) => (
                            <div className="p-0 pt-1" key={item.major_category_name}>
                                <button className='subCategorOutput' onClick={() => getMenuCategoryBymajorCategory(item.major_category_name)}>
                                    {item.major_category_name}
                                    {productMajorCategory === item.major_category_name ? "" : ""}
                                </button>
                                {/*        */}
                                {productMajorCategory === item.major_category_name && menuCategoryList.map((menuCategory: any, idx: any) => (
                                    <div className="ml-4" key={`${menuCategory.menu_category_name}`}>
                                        <label>
                                            <input className='w-4'
                                                type="radio"
                                                name="menuCategory"
                                                value={menuCategory.menu_category_name}
                                                checked={productMenuCategory === menuCategory.menu_category_name}
                                                onChange={(e) => setProductMenuCategory(e.target.value)}
                                            />
                                            {menuCategory.menu_category_name}
                                        </label>
                                    </div>
                                ))}
                            </div>
                        ))}
                    </div>
                </article>
                {/*   */}
                <article className='productSettingLine'>
                    <div className='productSettingName'></div>
                    <div className='productSettingValue imageLabel'>
                        <label htmlFor="file-upload" className="">
                             
                        </label>
                        &nbsp;{productImage ? productImage.name : "  "}
                        <input id="file-upload" type="file" onChange={(e) => { if (e.target.files && e.target.files.length > 0) { setProductImage(e.target.files[0]); } else { setProductImage(null); } }} style={{ display: 'none' }} />
                    </div>
                </article>
                <SettingProductCreateOption
                    setOptionState={setOption}
                />

            </section>
            <div className='text-right'>
                <button className="bg-gray-200" onClick={sendProductData}> </button>
            </div>
        </section>
    );
};

export default SettingProductAdd;