import { useState, useEffect } from "react";
import axios from "axios";


const SettingBranchMenuAdd = ({selectBranch}:any) => {
    const [categoryList, setCategoryList] = useState<any>([]);
    const [checkedCategories, setCheckCategories] = useState<any>([]);

    async function getMenuCategory() {
        try {
            const response = await axios.get(`https://${process.env.REACT_APP_SERVER_IP}/Setting/getMenuCategory`);
            if (response.status === 200) {
                // subCategoryList    
                console.log(response.data);
                setCategoryList(response.data);
            }
        } catch (error) {
            console.error('Error:', error);
        }
    }


    const handleCheckboxChange = (event: React.ChangeEvent<HTMLInputElement>, item: any) => {
        if (event.target.checked) {
            //  ,   
            setCheckCategories([...checkedCategories, item.menu_category_name]);

        } else {
            //   ,   
            setCheckCategories(checkedCategories.filter((checkedItem: string) => checkedItem !== item.menu_category_name));
        }
    };
    
    const crateBranchMenu = async () => {
        try {
            const response = await axios.post(`https://${process.env.REACT_APP_SERVER_IP}/Setting/createBranchMenu`, {
                branch_name: selectBranch,
                menu_category: checkedCategories
            });
            if (response.status == 200) {
                console.log(response.data);
            }
        } catch (error) {
            console.error('Error:', error);
        }

    }
    useEffect(() => {
        getMenuCategory();
    }, []);

    return (
        <div className='SettingBranchMenuAdd'>
            <h1>  : {selectBranch}</h1>
            <p>   </p>
            <p>     (   )</p>

            {categoryList.map((item: any) => (
                <div className="branchCheck" key={item.id}> {/* Key  */}
                    <label>
                        <input
                            type="checkbox"
                            checked={checkedCategories.includes(item.menu_category_name)}
                            onChange={(event) => handleCheckboxChange(event, item)}
                        />
                        <h2>{item.menu_category_name}</h2>
                    </label>
                </div>
            ))}
            <button onClick={() => crateBranchMenu()}> </button>

        </div>
    );
};

export default SettingBranchMenuAdd;