import React, { useEffect } from 'react';

function CallPage() {
  useEffect(() => {
    handleCall();
  }, []);

  const handleCall = () => {
    window.location.href = 'tel:00000000000';
  }

  return (
    <div>
      <button onClick={handleCall}> </button>
    </div>
  );
}

export default CallPage;
